import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)
const FOO1 =  r => require.ensure([],() => r(require('@/pages/FOO1.vue')),'login')
const FOO2 =  r => require.ensure([],() => r(require('@/pages/FOO2.vue')),'login')
const FOO3 =  r => require.ensure([],() => r(require('@/pages/FOO3.vue')),'login')
export default new Router({
   mode:"history",
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/demo1',
      name: 'FOO1',
      component: FOO1
    },{
      path: '/foo2',
      name: 'FOO2',
      component: FOO2
    },{
      path: '/foo3',
      name: 'FOO3',
      component: FOO3
    }
        
    
  ]
})



